* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color:#fff;
}

body {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.inpt {
  width: 100%;
  font-size: 3rem;
  padding: 1.5rem;
  background-color: rgb(41,41,56);
  color: white;

}

.buttons{
  display: flex;
  flex-wrap: wrap;
  transition: all 0.5s linear;

}

button {
  flex: 0 0 25%;
  padding: 0.25rem 0;
  transition: all 2s ease;
  
}

button:hover{
  background-color: cadetblue
}

.btn-orange {
  background-color: rgb(245,146,62);
  font-size: 4rem;
  color: white

}
.btn-grey{
  background-color: #cccccc;
  font-size: 4rem;
}